<template>
<section class="wrap has_header_wrap">
	<section class="wrap_in">
		<!-- 头部标题 -->
		<com-header titles="全部分类">
			<template  slot="style-def"></template>
		</com-header>
		<!-- 头部标题 end -->

		<!-- 搜索 -->
		<div class="sort_search">
			<form action="javascript:;" @click="jumpSearch">
				<div class="search">	
					<input 
						type="text" 
						placeholder="请输入药品" 
						v-model="searchCon"
						@keyup="searchKeyup" />
					<i v-if="isShowX" @click.stop="clearAll"></i>
				</div>
				<button type="submit"></button>
			</form>
		</div>
		<div class="sort_con">
			<div class="s_left">
				<ul>
					<!-- <li class="cur">
						<span>风湿关节</span>
					</li> -->
					<li 
						:class="{ 'cur': index+1==curKs }"
						v-for=" (list,index) in allSortList "
						@click="nextSort( list, index )">
						<span>{{ list.productTypeName }}</span>
						<div class="sort_childs">
							<div class="sort_two" v-for=" (listTwo,index) in list.productTypeList ">
								<div 
									class="sort_two_head"
									@click="jumpProduct(listTwo,index)">{{ listTwo.productTypeName }}</div>
								<div class="sort_three" v-for="(listThree,index) in listTwo.products">
									<!-- <a href="javascript:;" > /product?ptId=72269 -->
									<router-link :to="{ path: '/product/product_detail', query: {proid: listThree.productID} }" v-text="listThree.currentName">		
									<!-- </a> -->
									</router-link>
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
			<!-- <div class="s_right">
				<dl>
					<dt>{{ parentName }}</dt>
					<dd 
						@click="jumpProduct(cs,index)"
						v-for=" (cs,index) in childSort ">
						<p>{{ cs.productTypeName }}</p>
					</dd>
				</dl>
			</div> -->
		</div>
		<!-- 搜索 end -->
	</section>
</section>
</template>

<script>
import { Search, Cell } from 'mint-ui';

export default {
	name: 'Sort',
	data() {
		return {
			isShowX: false, //是否显示清除按钮
			searchCon: '', //搜索内容
			ks: [], //第一级分类
			curKs: 1, //当前科室序号
			childSort: [], //二级分类
			parentName: '',//二级分类的父级
			allSortList: [],//分类数据
		}
	},
	components: {	
		'mt-search': Search,
		'mt-cell': Cell,
	},
	created (){
		const _self = this;

		// this.$ajax({
		// 	// url: '/apiserver/productType/getAllOneLevelProductType',
		// 	url: _self.webRoot + '/productType/getAllOneLevelProductType',
		// }).then(function(res){
		// 	// console.log(res)
		// 	_self.ks = res.data;

		// 	//默认获取第一个分类的内容
		// 	_self.$ajax({
		// 		// url: '/apiserver/productType/getAllTwoLevelProductType',
		// 		url: _self.webRoot + '/productType/getAllTwoLevelProductType',
		// 		data: { parentTypeID: res.data[0].productTypeID }
		// 	}).then(function(datas){
		// 		// console.log(datas)
		// 		_self.parentName = res.data[0].productTypeName; //父级
		// 		_self.childSort = datas.data;
		// 	});
		// });
		
		//获取分类	
		this.$ajax({
			url: _self.webRoot + '/productType/getProductTypeList',
		})
		.then(function( res ){
			// console.log('获取分类：', res);
			if( res.data.data.length>0 ){
				_self.allSortList = res.data.data;
				
			}
		}).catch(function( error ){

		});
	},
	mounted () {
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//跳到搜索页
		jumpSearch (){
			// this.$router.replace({ path: '/search' });
			this.$router.push({ path: '/search' });
			// this.$router.go(-1);
		},
		//跳转商品列表
		jumpProduct (cs,index){
			// console.log(cs,index)
			this.$router.push({ path: '/product', query: { ptId: cs.productTypeID } })
		},
		// 点击查看下级分类
		nextSort ( item, index ){
			const _self = this;

			this.curKs = index+1; //当前科室
			this.$ajax({
				// url: '/apiserver/productType/getAllTwoLevelProductType',
				url: _self.webRoot + '/productType/getAllTwoLevelProductType',
				data: { parentTypeID: item.productTypeID }
			}).then(function(res){
				// console.log(res)
				_self.parentName = item.productTypeName; //父级
				_self.childSort = res.data;
			});
		},
		//监听搜索
		searchKeyup (){ 
			// console.log()
			if( !this.searchCon ){
				this.isShowX = true;
			}
		},
		//清除所有内容
		clearAll (){ 
			this.searchCon = '';
			this.isShowX = false;
		},
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	        // console.log('1')
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	        // console.log('2')
	      }
	    },
	}
}	
</script>

<style scoped>
	@import './Css/Sort.css';
</style>


